<template>
    <v-container>
    <v-data-table
      :headers="headers"
      :items="desserts"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>CRUD Clientes</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
                @click="llamarRegistro()">
                Nuevo
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>
  
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.identificacion"
                        label="Identificación"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.nombre"
                        label="Nombre"
                      ></v-text-field>
                    </v-col>                    
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.apellidos"
                        label="Apellidos"
                      ></v-text-field>
                    </v-col>

                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.direccion"
                        label="Dirección"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.email"
                        label="Email"
                      ></v-text-field>
                    </v-col>                    
                    <v-col
                      cols="12"
                      sm="6"
                      md="4"
                    >
                      <v-text-field
                        v-model="editedItem.telefono"
                        label="Teléfono"
                      ></v-text-field>
                    </v-col>                    
                  </v-row>
                </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="editarCliente(editedItem)"
                >
                 Guardar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">Está seguro de querer eliminar este cliente?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">

        <v-icon
          small
          class="mr-2"
          @click="editItem(item._id)"
        >
          mdi-pencil
        </v-icon>

        <v-icon
          small
          
          @click="eliminarCliente(item._id)"
        >
          mdi-delete
        </v-icon>

      </template>
      <template v-slot:no-data>
        <v-btn
          color="primary"
          @click="llamarRegistro()"
          
          
        >
          Inicializar
        </v-btn>
      </template>
    </v-data-table>
    </v-container>
  </template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: 'Identificación', value: 'identificacion' },
      { text: 'Nombre', value: 'nombre' },
      { text: 'Apellidos', value: 'apellidos' },
      { text: 'Dirección', value: 'direccion' },
      { text: 'Email', value: 'email' },      
      { text: 'Teléfono', value: 'telefono' }, 
      { text: 'Acciones', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem:[],
    defaultItem: {
      
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Editar' : 'Editar'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  created () {
    this.initialize()
    this.listarClientes();
  },

  methods: {
    initialize () {
      
    },

    editItem (item) {
      this.dialog = true
      console.log(item);
      this.axios.get(`/buscarParametro/${item}`)
      .then(res=>{
        this.editedItem=res.data
      })
    },

    deleteItem () {

      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },



deleteItemConfirm () {
      this.desserts.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },



    listarClientes(){
    this.axios.get('buscarTodo')
    .then((response)=>{
      this.desserts=response.data;
    })
    .catch((e)=>{
      console.log('error'+e)
    })
  },

editarCliente(item){
 this.axios.put(`/actualizar/${item._id}`,item)
 .then(res=>{
  const index=this.desserts.findIndex(n=>n._id===res.data._id)
  this.desserts[index].identificacion=res.data.identificacion;
  this.desserts[index].nombre=res.data.nombre;
  this.desserts[index].apellidos=res.data.apellidos;
  this.desserts[index].direccion=res.data.direccion;
  this.desserts[index].email=res.data.email;
  this.desserts[index].telefono=res.data.telefono; 
  
  this.$swal('success!',
                        'Falta Identificación!',
	                      'success');
                        this.close();
  
 }).catch(e=>{
  console.log(e.rersponse)
})
},

llamarRegistro(){
      this.$router.push('/registrarclientes')
      },

eliminarCliente(id){
  console.log(id);
  this.axios.delete(`/eliminarParametro/${id}`)
  .then(res=>{
    this.$swal('OK!',
                        'Registro ha sido eliminado!',
	                      'error');
    const index=this.desserts.findIndex(item=>item._id===res.data._id)
    this.desserts.splice(index,1);
    this.editedIndex=this.desserts.indexOf(item);
    this.editedItem=object.assign({},item)



  }).catch(e=>{
  console.log(e.response)
})

}

  },




}
</script>